import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Navigate, NavLink } from "react-router-dom";
import { Button } from "@mui/material";

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData("حسن ", "مرادی", 3810439479, "مرد", "09183468975"),
  createData("علی ", "پورمحمد", 210065948, "مرد", "0921564213"),
  createData("مریم ", "داودی", 2920654987, "زن", "0914164987"),
  createData("سوسن ", "خضری", 2870645236, "زن", "09183468975"),
  createData("سینا ", "اسماعیلی", 3720645921, "مرد", "09221976549"),
  createData("لیلا ", "مینایی", 383064597, "زن", "09112365497"),
  createData("شیما ", "حسن زاده", 10032654923, "زن", "09183468975"),
  createData("زهرا ", "کرمی", 16413645912, "زن", "09143815527"),
];

export default function BasicTable() {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow
            sx={{
              "&:last-child th": {
                borderColor: "#FF0000",
              },
              backgroundColor: "#E5E7E9",
            }}
          >
            <TableCell align="center">نام </TableCell>
            <TableCell align="center">نام خانوادگی</TableCell>
            <TableCell align="center">کدملی</TableCell>
            <TableCell align="center">جنسیت</TableCell>
            <TableCell align="center">موبایل</TableCell>
            <TableCell align="left"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows?.map((row, index) => (
            <TableRow
              key={row.index}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell component="th" scope="row" align="center">
                {row.name}
              </TableCell>
              <TableCell align="center">{row.calories}</TableCell>
              <TableCell align="center">{row.fat}</TableCell>
              <TableCell align="center">{row.carbs}</TableCell>
              <TableCell align="center">{row.protein}</TableCell>
              <TableCell align="right">
                <NavLink to="/patientinfo" state={rows[index]}>
                  <Button variant="contained" sx={{ color: "#0D24B8" }}>
                    بیشتر
                  </Button>
                </NavLink>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
